let loop_num = 0;
let deep_num = 0;
export const get_navigator_attr = params => {
    var result = {};

    for (let attr in params) {
        loop_num += 1;

        if (loop_num > 512) {
            return result;
        }

        let current = params[attr];

        if (typeof current === "function") {

        } else if (typeof current === "object") {
            deep_num += 1;

            if (deep_num > 5) {
                continue;
            }

            result[attr] = get_navigator_attr(current);
            deep_num -= 1;
        } else {
            result[attr] = current;
        }
    }

    return result;
}
