import React, {Component} from 'react'
import {
    List,
    NavBar,
    Space,
} from 'antd-mobile';
import {
    get_downlink,
    get_downlinkMax,
    get_effectiveType,
    get_rtt,
    get_saveData,
    get_type,
    is_support_network
} from "../util/network";
class Network extends Component {

    constructor(props) {
        super(props);

        this.state = {
            support_network: is_support_network(),
        };
    }

    back() {
        window.history.go(-1)
    }

    render() {
        return (
            <div style={{background: "#EFF3F8", overflowX: "hidden"}}>
                <NavBar back='' onBack={this.back} style={{background: "white"}}></NavBar>

                <Space direction='vertical' style={{width: "100%", margin: "30px 0"}}>
                    <List>
                        <List.Item description={this.state.support_network ? get_effectiveType() : ''}>
                            
                        </List.Item>
                        <List.Item description={this.state.support_network ? get_type() : ''}>
                            
                        </List.Item>
                        <List.Item description={this.state.support_network ? get_downlink() : ''}>
                            
                        </List.Item>
                        <List.Item description={this.state.support_network ? get_downlinkMax() : ''}>
                            
                        </List.Item>
                        <List.Item description={this.state.support_network ? get_rtt() : ''}>
                            
                        </List.Item>
                        <List.Item description={this.state.support_network ? (get_saveData() ? '' : '') : ''}>
                            /
                        </List.Item>
                        {/*<List.Item description={this.state.support_network ? (get_onchange() > 0 ? '' : '') : ''}>*/}
                        {/*    */}
                        {/*</List.Item>*/}
                    </List>
                </Space>
            </div>
        );
    }
}

export default Network;
