import React, {Component} from 'react'
import {
    List,
    NavBar,
    Space,
} from 'antd-mobile';
import {get_webdriver, has_webdriver} from "../util/navigator";
import {has_nightmareJS, has_phantomJS, has_playwright, has_selenium, use_webdriver} from "../util/browser";
import {get_maxTouchPoints, is_support_touch} from "../util/touch";
import {
    is_chrome,
    is_chromium,
    is_chromium86OrNewer,
    is_desksafari,
    is_webkit,
    is_webKit606OrNewer
} from "../util/browser_detect";
import Event from "../components/Event";
class Browser extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isTrusted_status: '',
        };

        this.myRef = React.createRef();
    }

    back() {
        window.history.go(-1)
    }

    render() {
        return (
            <div style={{background: "#EFF3F8", overflowX: "hidden"}}>
                <NavBar back='' onBack={this.back} style={{background: "white"}}></NavBar>

                <Space direction='vertical' style={{width: "100%", margin: "30px 0"}}>
                    <List header=''>
                        <List.Item description={has_webdriver() ? '' : ''}>
                             webdriver
                        </List.Item>
                        <List.Item description={JSON.stringify({value: get_webdriver()})}>
                            webdriver 
                        </List.Item>
                        <List.Item description={use_webdriver() ? '' : ''}>
                             Chrome Driver
                        </List.Item>
                        <List.Item description={has_selenium() ? '' : ''}>
                             Selenium
                        </List.Item>
                        <List.Item description={has_playwright() ? '' : ''}>
                             Playwright
                        </List.Item>
                        <List.Item description={has_phantomJS() ? '' : ''}>
                             PhantomJS
                        </List.Item>
                        <List.Item description={has_nightmareJS() ? '' : ''}>
                             NightmareJS
                        </List.Item>
                    </List>

                    <List header='Event.isTrusted '>
                        <Event
                            ref={this.myRef}
                            isTrusted_status={this.state.isTrusted_status}
                            onClick={(e) => {
                                this.setState({
                                    isTrusted_status: e.isTrusted === true ? 'true' : 'false',
                                })
                            }
                            }
                        />
                        <List.Item description="" clickable onClick={(e) => {
                            const node = this.myRef.current;
                            node.click();
                        }
                        }>
                             Event.isTrusted JS
                        </List.Item>
                    </List>

                    <List header=''>
                        <List.Item description={is_support_touch() ? '' : ''}>
                             Touch 
                        </List.Item>
                        <List.Item description={get_maxTouchPoints()}>
                            
                        </List.Item>
                    </List>


                    <List header=''>
                        <List.Item description={is_chrome() ? '' : ''}>
                             Chrome 
                        </List.Item>
                        <List.Item description={is_chromium() ? '' : ''}>
                             Chromium 
                        </List.Item>
                        <List.Item description={is_chromium86OrNewer() ? '' : ''}>
                             Chromium version  86
                        </List.Item>
                        <List.Item description={is_desksafari() ? '' : ''}>
                             Safari 
                        </List.Item>
                        <List.Item description={is_webkit() ? '' : ''}>
                             webkit 
                        </List.Item>
                        <List.Item description={is_webKit606OrNewer() ? '' : ''}>
                             webkit version  606 (Safari  12)
                        </List.Item>
                    </List>

                    <List header=''>
                        <List.Item
                            description={ window.performance.memory ? window.performance.memory.totalJSHeapSize + "/" + window.console.memory.totalJSHeapSize : ''}>
                            
                        </List.Item>
                        <List.Item
                            description={window.performance.memory ?window.performance.memory.usedJSHeapSize + "/" + window.console.memory.usedJSHeapSize: ''}>
                             JS segment
                        </List.Item>
                        <List.Item
                            description={ window.performance.memory ? window.performance.memory.jsHeapSizeLimit + "/" + window.console.memory.jsHeapSizeLimit: ''}>
                            
                        </List.Item>
                    </List>

                </Space>
            </div>
        );
    }
}

export default Browser;
