import React, {Component} from 'react'
import ReactJson from 'react-json-view'
import {
    List,
    NavBar,
    Space,
    Dialog,
    Divider, NoticeBar
} from 'antd-mobile';
import {
    all_languages,
    all_mimeTypes,
    all_plugins,
    get_deviceMemory, get_doNotTrack, get_hardwareConcurrency, get_javaEnabled, get_language,
    get_navigator_attr, get_pdfViewerEnabled, get_platform, get_userAgent, get_userAgentData, get_vendor,
} from '../util/navigator';

class Basic extends Component {
    constructor(props) {
        super(props);

        this.state = {
            navigator_attr: {},
        };
    }

    componentDidMount() {
        this.setState({
            navigator_attr: get_navigator_attr(window.navigator),
        })
    }

    back() {
        window.history.go(-1)
    }

    render() {
        return (
            <div style={{background: "#EFF3F8", overflowX: "hidden"}}>
                <NavBar back='' onBack={this.back} style={{background: "white"}}>Navigator </NavBar>
                <NoticeBar content='   HTTPS ' color='alert' closeable />
                <Space direction='vertical' style={{width: "100%", margin: "30px 0"}}>
                    <List>
                        <List.Item description='' clickable onClick={() =>
                            Dialog.alert({
                                content: (<ReactJson src={this.state.navigator_attr}/>),
                                closeOnMaskClick: true,
                            })
                        }>
                            
                        </List.Item>

                        <List.Item description={get_userAgent()}>
                            User-Agent

                            <Divider/>

                            <div style={{fontSize: 14, color: "#999"}}>{get_userAgent()}</div>
                        </List.Item>
                        <List.Item>
                            User-Agent Data

                            <Divider/>

                            <ReactJson src={get_userAgentData() ? get_userAgentData().toJSON() : {}}/>
                        </List.Item>
                        <List.Item description={get_platform()}>
                            
                        </List.Item>
                        <List.Item description={get_vendor()}>
                            
                        </List.Item>
                        <List.Item description={get_deviceMemory() ? get_deviceMemory() + " G" : ''}>
                             HTTPS 
                        </List.Item>
                        <List.Item description={get_hardwareConcurrency() + ' '}>
                            
                        </List.Item>
                        <List.Item>
                            
                            <Divider/>

                            <ReactJson src={all_plugins()}/>
                        </List.Item>
                        <List.Item>
                            
                            <Divider/>

                            <ReactJson src={all_mimeTypes()}/>
                        </List.Item>
                        <List.Item description={all_languages()}>
                            
                        </List.Item>
                        <List.Item description={get_language()}>
                            
                        </List.Item>
                        <List.Item description={get_doNotTrack() ? get_doNotTrack() : 'null'}>
                            Do Not Track 
                        </List.Item>
                        <List.Item description={get_javaEnabled() ? '' : ''}>
                            javaEnabled
                        </List.Item>
                        <List.Item description={get_pdfViewerEnabled() ? '' : ''}>
                            pdfViewerEnabled
                        </List.Item>
                    </List>
                </Space>
            </div>
        );
    }
}

export default Basic;
