import React, {Component} from 'react'
import ReactJson from 'react-json-view'
import {
    List,
    NavBar,
    Space,
    Dialog,
    Button
} from 'antd-mobile';
import {
    get_history_length,
    get_performance_type,
} from '../util/browser';
import {
    get_window_attr,
} from '../util/window';

class Basic extends Component {
    constructor(props) {
        super(props);

        this.state = {
            confirm_status: '',
            window_attr: {},
        };
    }

    componentDidMount() {
        this.setState({
            window_attr: get_window_attr(window),
        })
    }

    back() {
        window.history.go(-1)
    }

    render() {
        return (
            <div style={{background: "#EFF3F8", overflowX: "hidden"}}>
                <NavBar back='' onBack={this.back} style={{background: "white"}}>Window </NavBar>

                <Space direction='vertical' style={{width: "100%", margin: "30px 0"}}>
                    <List>
                        <List.Item description='' clickable onClick={(e) => {
                            console.log(e.pageX);
                            console.log(e.pageY);
                            console.log(e.clientX);
                            console.log(e.clientY);
                            Dialog.alert({
                                content: (<ReactJson src={this.state.window_attr}/>),
                                closeOnMaskClick: true,
                            })
                        }
                        }>
                             Window 
                        </List.Item>
                        <List.Item description={get_performance_type()}>
                            
                        </List.Item>
                        <List.Item description={get_history_length()}>
                            
                        </List.Item>
                        <List.Item extra={<Button size='small' color='warning'
                                                  onClick={() => alert("Alert ")}></Button>}>
                             Alert 
                        </List.Item>
                        <List.Item extra={<Button size='small' color='warning' onClick={() => {
                            const status = window.confirm("Confirm ");
                            this.setState({
                                confirm_status: status === true ? 'true' : 'false',
                            })
                        }}></Button>} description={`confirm${this.state.confirm_status}`}>
                             Confirm 
                        </List.Item>
                        <List.Item description='' clickable onClick={() => {
                            window.open('https://myip.ipip.net')
                        }}>
                             window.open 
                        </List.Item>
                    </List>

                </Space>
            </div>
        );
    }
}

export default Basic;
