import React, {Component} from 'react'
import {
    List,
    NavBar,
    Space,
} from 'antd-mobile';
import {is_support_cookie, is_support_indexdb, is_support_localStorage, is_support_session} from "../util/browser";

class API extends Component {
    back() {
        window.history.go(-1)
    }

    render() {
        return (
            <div style={{background: "#EFF3F8", overflowX: "hidden"}}>
                <NavBar back='' onBack={this.back} style={{background: "white"}}>API </NavBar>

                <Space direction='vertical' style={{width: "100%", margin: "30px 0"}}>
                    <List>
                        <List.Item description={is_support_cookie() ? '' : ''}>
                             Cookie
                        </List.Item>
                        <List.Item description={is_support_session() ? '' : ''}>
                             Session
                        </List.Item>
                        <List.Item description={is_support_indexdb() ? '' : ''}>
                             IndexDB
                        </List.Item>
                        <List.Item description={is_support_localStorage() ? '' : ''}>
                             LocalStorage
                        </List.Item>
                    </List>
                </Space>
            </div>
        );
    }
}

export default API;
