/**
 *  touch 
 * @returns {boolean}
 */
const is_support_touch = () => {
    let status = false;

    try {
        document.createEvent("TouchEvent");
        status = true;
    } catch (e) {
    }

    return status || "ontouchstart" in window;
}

/**
 * 
 * @returns {number}
 */
const get_maxTouchPoints = () => {
    return navigator.maxTouchPoints;
}

module.exports = {
    is_support_touch,
    get_maxTouchPoints,
}
