import React, {Component} from 'react'
import {
    List,
    NavBar,
    Space,
} from 'antd-mobile';

class Battery extends Component {
    constructor(props) {
        super(props);

        this.state = {
            is_charging: false,
            level: 0,
            support_battery: !!navigator.getBattery,
        };
    }

    componentDidMount() {
        let is_charging, charging_time, discharging_time, level;
        const that = this;

        // 
        if (this.state.support_battery) {
            navigator.getBattery().then(function (battery) {
                // 
                is_charging = battery.charging;
                // 0
                charging_time = battery.chargingTime;
                // 
                discharging_time = battery.dischargingTime;
                // 
                level = battery.level * 100;

                that.setState({
                    is_charging,
                    level
                })
            });
        }
    }

    back() {
        window.history.go(-1)
    }

    render() {
        return (
            <div style={{background: "#EFF3F8", overflowX: "hidden"}}>
                <NavBar back='' onBack={this.back} style={{background: "white"}}></NavBar>

                <Space direction='vertical' style={{width: "100%", margin: "30px 0"}}>
                    <List>
                        <List.Item
                            description={this.state.support_battery ? (this.state.is_charging ? '' : '') : ''}>
                            
                        </List.Item>
                        <List.Item description={this.state.support_battery ? this.state.level : ''}>
                            
                        </List.Item>
                    </List>
                </Space>
            </div>
        );
    }
}

export default Battery;
